import App from '../app';
import { getGlobalLifeCycles } from './globalLifeCycles';
import { isString, log } from './index';
import { rawWindow } from './raws';

const webComponentTagName = 'zmf-app';

const execLock = {};

export default function defineWebComponents() {
  if (rawWindow.customElements.get(webComponentTagName)) return;

  const globalLifeCycles = getGlobalLifeCycles();

  const template = rawWindow.document.createElement('template');
  template.innerHTML = `
    <zmf-app-head></zmf-app-head>
    <zmf-app-body></zmf-app-body>
  `.trim();

  class ZmfWebComponent extends HTMLElement {
    constructor() {
      super();
      // const shadow = this.attachShadow({ mode: 'open' });
      this.appId = '';
      this.app = null;
    }

    connectedCallback() {
      this.appendChild(template.content.cloneNode(true));

      const name = this.getAttribute('name');
      const module = this.getAttribute('module');

      if (!isString(name)) {
        log.error('options `name` type must be a string.');
        return;
      }

      if (!/^\w+\/\w+$/.test(module)) {
        log.error('options `module` not available, e.g. remoteApp1/someModule');
        return;
      }

      const appId = `${name}_${module}`;
      const shadow = this;
      this.appId = appId;

      if (execLock[this.appId]) return;
      execLock[this.appId] = true;

      setTimeout(() => {
        this.dispatchEvent(new CustomEvent(`${appId}:beforeMount`));
        globalLifeCycles['beforeMount'](appId);
      }, 0);

      const app = new App({
        shadow,
        name,
        module,
        id: appId
      }).start();

      app
        .then(app => {
          this.app = app;

          if (typeof app.remoteModuleExports.mount !== 'function') {
            log.error(
              `remote module ${app.module} rendered by zmf-app must export mount function.`
            );
            return;
          }

          app.remoteModuleExports.mount(app.shadow.shadowBody);
          this.dispatchEvent(new CustomEvent(`${appId}:onMount`));
          globalLifeCycles['onMount'](appId);
        })
        .catch(e => {
          this.dispatchEvent(
            new CustomEvent(`${appId}:onError`, {
              detail: e
            })
          );

          globalLifeCycles['onError'](e, appId);
        })
        .finally(() => {
          execLock[this.appId] = false;
        });
    }

    disconnectedCallback() {
      this.dispatchEvent(new CustomEvent(`${this.appId}:onUnmount`));
      globalLifeCycles['onUnmount'](this.appId);
      if (this.app) this.app.destroy();
    }
  }

  rawWindow.customElements.define(webComponentTagName, ZmfWebComponent);
}
