import { defer } from './index';

let currentRuningApp = '';

export function setCurrentRuningApp(appId) {
  if (currentRuningApp === appId) return;
  currentRuningApp = appId;
  defer(function () {
    currentRuningApp = '';
  });
}

export function getCurrentRuningApp() {
  return currentRuningApp;
}

export function clearCurrentRuningApp() {
  currentRuningApp = '';
}
