import { bindLexicalScope } from './index';
import { rawWindow } from './raws';

function getModuleFactorys(containerId) {
  return rawWindow[`__ZMF_${containerId.toUpperCase()}_WEBPACK_MODULES__`];
}

function getRemoteAppOriginWebpackRequire(containerId) {
  return rawWindow[`__ZMF_${containerId.toUpperCase()}_WEBPACK_REQUIRE__`];
}

class federationModuleRequire {
  constructor(options) {
    this.cache = {};
    this.options = options;
    this.moduleFactorys = getModuleFactorys(this.options.containerId);
    this.bindedThisRequire = this.copyOriginRequireMethods(
      getRemoteAppOriginWebpackRequire(this.options.containerId)
    );
  }

  copyOriginRequireMethods(webpackRequire) {
    // 传入 module factory 里执行防止 this 丢失
    const newRequire = this.require.bind(this);
    Object.keys(webpackRequire).forEach(key => (newRequire[key] = webpackRequire[key]));

    newRequire.__containerId__ = this.options.containerId;
    return newRequire;
  }

  require(mid) {
    // 模块 id 生产模式下可能是数字，要转成字符串，才能去 consumesIds 里查找
    const moduleId = typeof mid === 'number' ? '' + mid : mid;
    const remoteAppOriginWebpackRequire = getRemoteAppOriginWebpackRequire(
      this.options.containerId
    );
    //  "webpack/sharing/consume/default/xx/xx?4a07"
    if (remoteAppOriginWebpackRequire.f.consumes.consumesIds.includes(moduleId)) {
      return remoteAppOriginWebpackRequire(moduleId);
    }

    const cachedModule = this.cache[moduleId];

    const { proxyWindow, proxyDocument } = this.options;

    const moduleFactorys = this.moduleFactorys;

    if (cachedModule !== undefined) {
      if (cachedModule.error !== undefined) throw cachedModule.error;
      return cachedModule.exports;
    }

    const webpackModule = (this.cache[moduleId] = {
      id: moduleId,
      loaded: false,
      exports: {}
    });

    try {
      const factory = bindLexicalScope(
        `return  ${moduleFactorys[moduleId].toString()}`,
        proxyWindow,
        proxyDocument
      );

      const execOptions = {
        id: moduleId,
        module: webpackModule,
        factory,
        // 传入 module factory 里执行下一个 require 防止 this 丢失
        require: this.bindedThisRequire
      };

      // 这里做个兼容， 生产环境是没有 HRM 代码的
      (this.bindedThisRequire.i || []).forEach(handler => handler(execOptions));

      execOptions.factory.call(
        webpackModule.exports,
        webpackModule,
        webpackModule.exports,
        execOptions.require
      );
    } catch (e) {
      webpackModule.error = e;
      throw e;
    }

    webpackModule.loaded = true;
    return webpackModule.exports;
  }
}

export default federationModuleRequire;
