import css from './css';
import { bindLexicalScope, isAvailableUrl, pFetchSource, pLoadScript } from './utils';
import { getApp, getApps, removeApp, setApp } from './utils/appManage';
import { getCurrentRuningApp, setCurrentRuningApp } from './utils/appRuningManage';
import deferredPromise from './utils/deferredPromise';
// import { EventCenterForApp } from './utils/eventCenter';
import federationModuleRequire from './utils/federationModuleRequire';
import { rawAppendChild, rawDoc, rawInsertBefore, rawWindow } from './utils/raws';

function getZmfConfig() {
  return rawWindow.__ZMF__ || {};
}

function getEntry(containerName) {
  return `${getZmfConfig()[containerName]}/${containerName}.remoteEntry.js`;
}

function getModuleFactoryId(fn) {
  const str = fn
    .toString()
    // (/*! ./x/y.js */ "./x/y.js") => ("./x/y.js")
    .replace(/\s*\/\*.+?\*\/\s*/, '');

  // () => ((__webpack_require__("./src/bootstrap.js")))
  // function(){return n(1794)}
  let ret = /['"](.+)['"]/.exec(str) || /\((\d+)\)/.exec(str);
  if (!ret) {
    throw `function \`${str}\` can not match module factory id.`;
  }
  return ret[1];
}

const domAppendOrInsertHanders = {
  '[object HTMLStyleElement]'(dom, containerId) {
    if (dom.textContent) {
      parseInlineStyle(dom.textContent, containerId).then(code => {
        dom.textContent = code;
      });
    }
  },

  '[object HTMLLinkElement]'(dom, containerId) {
    const href = dom.href;
    const inlineStyleEl = rawDoc.createElement('style');
    let ctx = this;
    inlineStyleEl.dataset.f = dom.href;
    inlineStyleEl.onload = dom.onload || function onload() {};
    inlineStyleEl.onerror = dom.onerror || function onerror() {};

    parseLinkStyle(href, containerId).then(scopedCSSCode => {
      const textNode = document.createTextNode(scopedCSSCode);
      textNode.__scoped__ = true;
      inlineStyleEl.appendChild(textNode);
    });

    return {
      resolvedCtx: ctx,
      resolvedDom: inlineStyleEl
    };
  },

  '[object HTMLScriptElement]'(dom, currentRuningAppId) {
    if (!isAvailableUrl(dom.src)) {
      return false;
    } else {
      const onload = (dom.onload || function onload() {}).bind(this);
      const onerror = (dom.onerror || function onerror() {}).bind(this);

      pFetchSource(dom.src)
        .then(ret => {
          if (ret.loaded) return;
          const currentRuningApp = getApp(currentRuningAppId);
          bindLexicalScope(
            ret.code,
            currentRuningApp.proxyWindow,
            currentRuningApp.proxyWindow.proxyDoc
          );

          onload();
          this.appendChild(rawDoc.createComment(` replace ${dom.src} by micro-module-federation `));
          ret.loaded = true;
        })
        .catch(e => {
          onerror(e);
        });

      return true;
    }
  }
};

function getContainerIdFromAppid(id) {
  // xx_containerId/moduleName;
  return id.split('_').pop().split('/')[0];
}

function parseLinkStyle(url, scopeAttr) {
  return pFetchSource(url).then(ret => {
    if (ret.loaded) return;
    const scopedStyle = parseStyle(ret.code, scopeAttr, url);
    ret.load = true;
    return scopedStyle;
  });
}

function parseInlineStyle(code, scopeAttr, baseUrl) {
  return Promise.resolve(parseStyle(code, scopeAttr, baseUrl));
}

function parseStyle(code, scopeAttr, baseUrl) {
  const cssAst = css.parse(code, {
    baseUrl,
    scopeSelectorPrefix: `zmf-app[data-cid=${scopeAttr}]`,
    selectorsMap: {
      html: 'zmf-app',
      body: 'zmf-app-body'
    }
  });
  return css.stringify(cssAst);
}

const patchFns = {
  appendChild: function appendChild(dom) {
    const domType = Object.prototype.toString.call(dom);
    const currentRuningAppId = getCurrentRuningApp();
    const containerId = getContainerIdFromAppid(currentRuningAppId);
    let ctx = this;
    let replacementDom = dom;

    // 外链样式
    if (containerId && replacementDom instanceof HTMLLinkElement && replacementDom.href) {
      const { resolvedDom, resolvedCtx } = domAppendOrInsertHanders[domType].bind(this)(
        replacementDom,
        containerId
      );
      return rawAppendChild.call(resolvedCtx, resolvedDom);
    } else if (
      containerId &&
      replacementDom instanceof HTMLStyleElement &&
      replacementDom.textNode
    ) {
      // 内联样式只是转换 textContent 就行
      domAppendOrInsertHanders[domType].bind(this)(replacementDom, containerId);
    } else if (
      containerId &&
      replacementDom.nodeName === '#text' &&
      ctx.nodeName.toLowerCase() === 'style' &&
      !replacementDom.__scoped__
    ) {
      domAppendOrInsertHanders['[object HTMLStyleElement]'].bind(this)(replacementDom, containerId);
    }

    // 当执行真正的 factory 时候的 dom 操作.
    if (
      currentRuningAppId &&
      // INIT 初始化下载 thunk 阶段的不用绑定 win, doc
      !/INIT:/.test(currentRuningAppId) &&
      domAppendOrInsertHanders[domType] &&
      domAppendOrInsertHanders[domType].bind(this)(dom, currentRuningAppId)
    ) {
      return;
    }

    return rawAppendChild.call(ctx, replacementDom);
  },

  insertBefore: function insertBefore(dom, referenceNode) {
    const domType = Object.prototype.toString.call(dom);
    const currentRuningAppId = getCurrentRuningApp();
    const containerId = getContainerIdFromAppid(currentRuningAppId);
    let ctx = this;

    // 外链样式
    if (containerId && dom instanceof HTMLLinkElement && dom.href) {
      const { resolvedDom, resolvedCtx } = domAppendOrInsertHanders[domType].bind(this)(
        dom,
        containerId
      );
      return rawInsertBefore.call(resolvedCtx, resolvedDom, referenceNode);
    } else if (containerId && dom instanceof HTMLStyleElement) {
      domAppendOrInsertHanders[domType].bind(this)(dom, containerId);
    } else if (
      containerId &&
      dom.nodeName === '#text' &&
      ctx.nodeName.toLowerCase() === 'style' &&
      !dom.__scoped__
    ) {
      domAppendOrInsertHanders['[object HTMLStyleElement]'].bind(this)(dom, containerId);
    }

    if (
      currentRuningAppId &&
      // INIT 初始化下载 thunk 阶段的不用绑定 win, doc
      !/INIT:/.test(currentRuningAppId) &&
      domAppendOrInsertHanders[domType] &&
      domAppendOrInsertHanders[domType].bind(this)(dom, currentRuningAppId)
    ) {
      return;
    }
    return rawInsertBefore.call(ctx, dom, referenceNode);
  }
};

function patchDomAppendOrInsert() {
  if (Node.prototype.appendChild === patchFns.appendChild) return;
  Object.assign(Node.prototype, patchFns);
}

function freePatchDomAppendOrInsert() {
  Node.prototype.appendChild = rawAppendChild;
  Node.prototype.insertBefore = rawInsertBefore;
}

async function loadFederationModule(container, moduleName, appid) {
  // eslint-disable-next-line
  await __webpack_init_sharing__('default');

  // eslint-disable-next-line
  await rawWindow[container].init(__webpack_share_scopes__.default);
  setCurrentRuningApp(`INIT:${appid}`);
  return rawWindow[container].get(`./${moduleName}`);
}

const nodeNameMap = {
  head: 'zmf-app-head',
  body: 'zmf-app-body'
};

const documentOperateRedirect = {
  head() {
    return this.querySelector('zmf-app-head');
  },

  body() {
    return this.querySelector('zmf-app-body');
  },

  images() {
    // HTMLCollection
    return this.getElementsByTagName('img');
  },

  addEventListener(...args) {
    return this.addEventListener.apply(this, args);
  },

  removeEventListener(...args) {
    return this.removeEventListener.apply(this, args);
  },

  querySelector(selector) {
    const lowerCaseSelector = selector.toLowerCase();
    return this.querySelector(
      nodeNameMap[lowerCaseSelector] ? nodeNameMap[lowerCaseSelector] : selector
    );
  },

  querySelectorAll(selector) {
    const lowerCaseSelector = selector.toLowerCase();
    return this.querySelectorAll(
      nodeNameMap[lowerCaseSelector] ? nodeNameMap[lowerCaseSelector] : selector
    );
  },

  // Element.prototype 下没有 getElementById
  getElementById(id) {
    return this.querySelector(`#${id}`);
  },

  getElementsByClassName(cls) {
    return this.getElementsByClassName(cls);
  },

  getElementsByTagName(tagName) {
    return this.getElementsByTagName(tagName);
  },

  getElementsByTagNameNS(...args) {
    return this.getElementsByTagNameNS.apply(this, args);
  },

  getAnimations(...args) {
    return this.getAnimations.apply(this, args);
  }
};

const proxyWindows = {};

// 同一个 host 下的不用 module 共享一个 proxyWindow
function createProxyWindow(containerId, moduleId, appName) {
  if (proxyWindows[containerId]) return proxyWindows[containerId];
  const fakeWindow = {
    rawWindow: rawWindow,
    document: rawDoc,
    __isZMF__: true,
    __MICRO_MODULE_ID__: moduleId,
    __APP_NAME__: appName
  };

  const proxyWindow = new Proxy(fakeWindow, {
    get(target, prop) {
      const value = target[prop] || rawWindow[prop];
      if (typeof value === 'function') {
        return value.bind(rawWindow);
      }
      return value;
    },

    set(obj, prop, value) {
      obj[prop] = value;
      return true;
    }
  });
  return (proxyWindows[containerId] = proxyWindow);
}

export default class App {
  constructor(options) {
    this.options = options;
    this.appName = options.name;
    this.appId = this.options.id;
    this.module = this.options.module;
    this.containerId = this.options.module.split('/')[0];
    this.remoteEntry = '';
    this.proxyWindow = createProxyWindow(this.containerId, this.appId, this.appName);
    // this.proxyWindow.microApp = new EventCenterForApp(this.appName);
    this.remoteModuleExports = null;
    this.deferredPromise = deferredPromise();
    this.init();
  }

  createProxyDoc() {
    const shadowRoot = this.shadow.shadowRoot;
    const appId = this.appId;
    return new Proxy(rawDoc, {
      get(target, prop) {
        setCurrentRuningApp(appId);
        if (documentOperateRedirect[prop]) {
          // e.g. document.querySelector, addEventListener
          if (typeof target[prop] === 'function') {
            return documentOperateRedirect[prop].bind(shadowRoot);
          } else {
            // e.g. document.head
            return documentOperateRedirect[prop].call(shadowRoot);
          }
        }

        if (typeof target[prop] === 'function') {
          return target[prop].bind(rawDoc);
        }
        return target[prop];
      },

      set(target, prop, value) {
        // e.g. dom.onclick = function() {}
        if (/^(on)/.test(prop)) {
          shadowRoot[prop] = value;
        } else {
          target[prop] = value;
        }
        return true;
      }
    });
  }

  init() {
    setApp(this.appId, this);
    this.resolveShadow();
    this.executeRemoteEntry();
  }

  resolveShadow() {
    const shadow = this.options.shadow;
    const shadowRoot = shadow;

    const containerId = this.options.module.split('/')[0];
    shadow.dataset.id = this.options.id;
    shadow.dataset.f = getZmfConfig()[containerId];
    shadow.dataset.cid = containerId;
    this.shadow = {
      shadow,
      shadowRoot,
      shadowHead: shadowRoot.querySelector('zmf-app-head'),
      shadowBody: shadowRoot.querySelector('zmf-app-body')
    };
  }

  async executeRemoteEntry() {
    const { module } = this.options;

    const [containerId, moduleName] = module.split('/');

    this.remoteEntry = getEntry(containerId);
    await pLoadScript(this.remoteEntry);
    patchDomAppendOrInsert();

    const factory = await loadFederationModule(containerId, moduleName, this.appId);

    const moduleId = getModuleFactoryId(factory);
    this.proxyWindow.proxyDoc = this.createProxyDoc(this.appId);

    const requireInstance = new federationModuleRequire({
      containerId,
      proxyWindow: this.proxyWindow,
      proxyDocument: this.proxyWindow.proxyDoc
    });

    this.remoteModuleExports = requireInstance.require(moduleId);
    this.deferredPromise.resolve(this);
  }

  start() {
    return this.deferredPromise;
  }

  destroy() {
    removeApp(this.appId);

    if (!getApps().length) {
      freePatchDomAppendOrInsert();
    }
  }
}
