import pMemoize from 'p-memoize';
import deferredPromise from './deferredPromise';
import { rawWindow } from './raws';

export const emptyFn = () => {};
export const isString = p => Object.prototype.toString.call(p) === '[object String]';
export const isFunction = p => typeof p === 'function';
export const isDom = p => p instanceof Element;
export const defer = Promise.resolve().then.bind(Promise.resolve());

export const fetch = (() => {
  const cache = {};
  return function fetchSource(url) {
    if (cache[url]) return Promise.resolve(cache[url]);

    return rawWindow.fetch(url, { mode: 'cors' }).then(async r => {
      const code = await r.text();
      return (cache[url] = {
        code,
        loaded: false
      });
    });
  };
})();

export const isAvailableUrl = (url, showMsg = true) => {
  try {
    new URL(url);
    return true;
  } catch (e) {
    if (showMsg) {
      log.error(e);
    }
    return false;
  }
};

export const bindLexicalScope = (code, proxyWindow, proxyDocument) => {
  return new Function(
    'window',
    'document',
    `
       ${code};
    `.trim()
  )(proxyWindow, proxyDocument);
};

export const pFetchSource = pMemoize(fetch);

export const loadScript = url => {
  const defer = deferredPromise();
  let doc = rawWindow.document,
    scriptDomInstance;
  const scripts = rawWindow.document.getElementsByTagName('script');

  // cache
  for (let i = 0; i < scripts.length; i++) {
    if (scripts[i].src === url) {
      scriptDomInstance = scripts[i];
      defer.resolve();
      break;
    }
  }

  if (!scriptDomInstance) {
    scriptDomInstance = doc.createElement('script');
    scriptDomInstance.src = url;
    scriptDomInstance.onload = function onload() {
      defer.resolve();
    };
    scriptDomInstance.onerror = function onError(e) {
      defer.reject(e);
    };
    doc.head.appendChild(scriptDomInstance);
  }

  return defer;
};

export const pLoadScript = pMemoize(loadScript);

export const pathJoin = (basePath, joinPath) => {
  // 是带域名的绝对路径
  if (isAvailableUrl(joinPath, false)) {
    return joinPath;
  }

  const urlInfo = new URL(basePath);

  // https://a.com + /a/b.xxx
  if (/^\//.test(joinPath)) {
    return urlInfo.origin + joinPath;
  }

  // https://a.com/a/b/c.xxx + ./m/y.ttt
  if (/^.\//.test(joinPath)) {
    const paths = urlInfo.pathname.split('/');
    paths.pop();
    return urlInfo.origin + paths.join('/') + joinPath;
  }

  // https://a.com/a/b/c.xxx + ../../m/y.ttt
  if (/^(\.\.\/)/.test(joinPath)) {
    const parentLevelLength = (joinPath.match(/\.\.\//g) || []).length;
    const paths = urlInfo.pathname.split('/');
    paths.pop();
    const targetPath = joinPath.replace(/\.\.\//g, '');
    return (
      urlInfo.origin + paths.slice(0, paths.length - parentLevelLength).join('/') + '/' + targetPath
    );
  }

  return joinPath;
};

export const log = {
  info(msg) {
    console.info(`INFO: ${msg}`);
  },

  warn(msg) {
    console.warn(`WARN: ${msg}`);
  },

  error(msg) {
    console.error(`ERROR: ${msg}`);
  }
};
