import { emptyFn } from './utils';
import defineWebComponent from './utils/defineWebComponent';
import Event from './utils/eventCenter';
import { setGlobalLifeCycle } from './utils/globalLifeCycles';
const lifeCycleNames = ['beforeMount', 'onMount', 'onUnmount', 'onError'];

function getLifeCycles(options) {
  return Object.keys(options).filter(
    propName => lifeCycleNames.includes(propName) && typeof options[propName] === 'function'
  );
}

// 防止多次 start
let isStart = false;

const defaultOptions = {
  beforeMount: emptyFn,
  onMount: emptyFn,
  onUnmount: emptyFn,
  onError: emptyFn
};
export function startMMF(options = defaultOptions) {
  if (isStart) return;
  // window.baseApp = new EventCenterForApp('baseApp');
  const mergedOpts = Object.assign({}, defaultOptions, options);
  getLifeCycles(mergedOpts).forEach(propName => setGlobalLifeCycle(propName, mergedOpts[propName]));
  defineWebComponent();
  isStart = true;
}

export const mmfEvent = Event;
