/* eslint-disable */
import React, {useState, useEffect} from 'react';

import logo from './logo.svg';
import './App.css';
import { Button, Modal,  Space, Table, Tag, message } from 'antd';
import 'antd/dist/antd.css';
import {withRouter} from 'react-router-dom';
import Player from './player';
import * as zdi from '@zhihu/design-icons';
import { mmfEvent } from '@micromodule/core';
import { list } from 'postcss';



const { Column, ColumnGroup } = Table;



const data = [
  {
    key: '1',
    firstName: 'John',
    lastName: 'Brown',
    age: 32,
    address: 'New York No. 1 Lake Park',
    tags: ['nice', 'developer'],
  },
  {
    key: '2',
    firstName: 'Jim',
    lastName: 'Green',
    age: 42,
    address: 'London No. 1 Lake Park',
    tags: ['loser'],
  },
  {
    key: '3',
    firstName: 'Joe',
    lastName: 'Black',
    age: 32,
    address: 'Sidney No. 1 Lake Park',
    tags: ['cool', 'teacher'],
  },
];

function App(props) {
  
  
  const [list, setList] = useState([]);
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [isModalVisible2, setIsModalVisible2] = useState(false);

  const clickHandle = () => {

    props.history.push('/nextPage');
  }

  function clickHandle2() {
    setIsModalVisible2(!isModalVisible2);
  }

  function handleAddSomething() {
    const s = document.createComment('heiheihei');
    document.head.appendChild(s);
  }

  useEffect(() => {
    mmfEvent.on('HOST', function(d) {
      message.info(d);
    })
  }, [])


  function sendParent() {
    mmfEvent.emit('来自子应用数据: ' + Math.random())
  }
  

  return (
    <div style={{border: '1px solid #fff'}} className="App">
      <i className="fa fa-car"></i>
      <i className="fa fa-car" style={{color: 'green'}}></i>
      {
        list.map(t => <div key={Math.random()}>{t}</div>)
      }
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        
        <h1 onClick={handleAddSomething}>
          我是一个远程入口模块
        </h1>
     
        <Space><Button type="primary"  onClick={clickHandle}>点我跳转页面</Button></Space>
        <Space><Button type="primary"  onClick={clickHandle2}>点我加载播放器</Button></Space>
        <Space><Button type="primary"  onClick={sendParent}>向父应用发送数据</Button></Space>
       
        
        
        <br />
        <Modal title="Basic Modal" visible={isModalVisible2} onOk={clickHandle2} onCancel={clickHandle2}>
            <Player />
        </Modal>
      


        <Table dataSource={data}>
          <ColumnGroup title="Name">
            <Column title="First Name" dataIndex="firstName" key="firstName" />
            <Column title="Last Name" dataIndex="lastName" key="lastName" />
          </ColumnGroup>
          <Column title="Age" dataIndex="age" key="age" />
          <Column title="Address" dataIndex="address" key="address" />
          <Column
            title="Tags"
            dataIndex="tags"
            key="tags"
            render={(tags) => (
              <>
                {tags.map((tag) => (
                  <Tag color="blue" key={tag}>
                    {tag}
                  </Tag>
                ))}
              </>
            )}
          />
          <Column
            title="Action"
            key="action"
            render={(_, record) => (
              <Space size="middle">
                <a>Invite {record.lastName}</a>
                <a>Delete</a>
              </Space>
            )}
          />
        </Table>


       



      </header>
    </div>

  );
}



export default withRouter(App);
